import {memo} from "react"
import { useHistory } from "react-router-dom"

const Header=memo(()=>{
    const history=useHistory()
    return(
        <div className="header">
                <ul>
                    <li className="list1"><p onClick={()=>{history.push("/community")}}>トップ</p></li>
                    <li className="list1"><p onClick={()=>{history.push("/community/course")}}>授業</p></li>
                    <li className="list1"><p onClick={()=>{history.push("/community/reservation")}}>予約・申し込み</p></li>
                    <li className="list2"><p onClick={()=>{history.push("/community/question")}}>お問い合わせ</p></li>
                    <div className="clear"></div>
                </ul>
        </div>
    )
})
export default Header